#contact {
  background-color: $background-colour;
  background-image: url("imgs/background.jpg");
  background-size: cover;
  background-position: -6em bottom; /* Fix this position */
  height: 6em; /* Fix this height */
  color: white;
  text-align: right;
}

.contact__title {
  font-size: 1.5rem;
  font-weight: 700;
  color: $primary-colour;
}

.contact__icon {
  margin-left: .5em;
}

.contact__icons a,
.contact__attribute {
  color: white;
}